    }
    
    .th-boxscore-toggle .header-main-text,
    .th-boxscore-toggle .boxscore-label {
        font-size: 0.5625rem;
    }
    
    .boxscore-toggle-icon {
        font-size: 0.5625rem;
    }
    
    .picks-table td {
        padding: 6px 4px;
        font-size: var(--text-xs);
        line-height: 1.35;
    }
    
    .team-name-full {
        font-size: var(--text-sm);
    }
    
    .date-value {
        font-size: var(--text-sm);
    }
    
    .time-value {
